import React from "react"
import async from "../components/Async"
import * as Icon from "@material-ui/icons";

// eslint-disable-next-line no-restricted-globals
const clusterContext = location.pathname.substring(0, location.pathname.indexOf("/", 1))

// CLuster
const Cluster = async(() => import("../pages/cluster"));
const User = async(() => import("../pages/userManagement"));
const Role = async(() => import("../pages/role"));

// Dashboard components
const Dashboard = async(() => import("../pages/dashboard"));

// Auth components
const SignIn = async(() => import("../pages/auth/SignIn"));
const SignUp = async(() => import("../pages/auth/SignUp"));
const ResetPassword = async(() => import("../pages/auth/ResetPassword"));
const Page404 = async(() => import("../pages/auth/Page404"));
const Page500 = async(() => import("../pages/auth/Page500"));
const EditUser = async(() => import("../pages/userManagement/EditUser"));

// Analysis components
const Dictionary = async(() => import("../pages/analysis/dictionary"));
const Tools = async(() => import("../pages/analysis/tools"));
const RankingTuning = async(() => import("../pages/analysis/rankingTuning"));

// Indices components
const Templates = async(() => import("../pages/indices/indexTemplates"));
const TemplateNew = async(() => import("../pages/indices/indexTemplates/new"));
const TemplateEdit = async(() => import("../pages/indices/indexTemplates/edit"));
const TemplateView = async(() => import("../pages/indices/indexTemplates/view"));

const ClusterData = async(() => import("../pages/indices/cluster"));
const Index = async(() => import("../pages/indices/index"));
const IndexDetail = async(() => import("../pages/indices/index/Detail"));
const Collection = async(() => import("../pages/indices/collection"));
const CollectionDetail = async(() => import("../pages/indices/collection/Detail"));
const Jdbc = async(() => import("../pages/indices/jdbc"));
const Pipeline = async(() => import("../pages/indices/pipeline"));

// Search components
const QueryTest = async(() => import("../pages/search/queryTest"));
const ReferenceUI = async(() => import("../pages/search/referenceUI"));
const ReferenceUIResult = async(() => import("../pages/search/referenceUI/result"));

// Management components
const ServerManagement = async(() => import("../pages/management/serverManagement"));
const ApiManagement = async(() => import("../pages/management/apiManagement"));
const ClusterSettings = async(() => import("../pages/management/clusterSettings"));

//  

const homeRoutes = { id: "auth", path: "/", component: SignIn }

const clusterRoutes = { id: "", path: "/cluster", icon: <Icon.GroupWork/>, containsHome: true, component: Cluster, children: null };

const userRoutes = {id: "", path: "/user", icon: <Icon.AccountBox/>, component: User, children: null};

const roleRoutes = {id: "", path: "/role", icon: <Icon.LockOpen/>, component: Role, children: null};

const editUserRoutes = {id: "", path: "/my", component: EditUser, hidden: true};

//  
const dashboardRoutes = {id: "", path: `${clusterContext}/dashboard`, icon: <Icon.Dashboard/>, component: Dashboard, children: null};

const analysisRoutes = [
    {header: "", id: "", path: `${clusterContext}/analysis/dictionary`, icon: <Icon.ImportContacts/>, component: Dictionary, children: null},
    {id: "", path: `${clusterContext}/analysis/tools`, icon: <Icon.Poll/>, component: Tools, children: null},
    {id: "", path: `${clusterContext}/analysis/ranking-tuning`, icon: <Icon.Build/>, component: RankingTuning, children: null}
];

const indicesRoutes = [

    {header: "", id: "", path: `${clusterContext}/indices-cluster`, icon: <Icon.GroupWork/>, component: ClusterData, children: null},
    {id: "", path: `${clusterContext}/indices-collections`, icon: <Icon.CollectionsBookmark/>, component: Collection, children: null},
    {id: "", path: `${clusterContext}/indices-collections/*`, component: CollectionDetail, children: null, hidden: true},

    {id: "", path: `${clusterContext}/indices-templates`, icon: <Icon.FileCopy/>, component: Templates, children: null},
    {id: "", path: `${clusterContext}/indices-templates/new`, component: TemplateNew, hidden: true},
    {id: "", path: `${clusterContext}/indices-templates/*/edit`, component: TemplateEdit, hidden: true},
    {id: "", path: `${clusterContext}/indices-templates/*`, component: TemplateView, hidden: true},

    {id: "", path: `${clusterContext}/indices`, icon: <Icon.List/>, component: Index, children: null},
    {id: "", path: `${clusterContext}/indices/*`, component: IndexDetail, children: null, hidden: true},

    {id: "JDBC", path: `${clusterContext}/indices-jdbc`, icon: <Icon.Storage/>, component: Jdbc, children: null},
    {id: "", path: `${clusterContext}/indices-pipeline`, icon: <Icon.FilterList/>, component: Pipeline, children: null},
];

const searchRoutes = [
    {header: "", id: "", path: `${clusterContext}/search/query-test`, icon: <Icon.Search/>, component: QueryTest, children: null},
    {id: "UI", path: `${clusterContext}/search/reference-ui`, icon: <Icon.AspectRatio/>, icon2: <Icon.Launch />, component: ReferenceUI, children: null},
    {id: "", path: `${clusterContext}/search`, component: ReferenceUIResult, children: null, hidden: true},
];

const managementRoutes = [
    {header: "", id: "", path: `${clusterContext}/management/server`, icon: <Icon.Memory/>, component: ServerManagement, children: null},
    {id: "API", path: `${clusterContext}/management/api`, icon: <Icon.CallSplit/>, component: ApiManagement, children: null},
    {id: "", path: `${clusterContext}/management/settings`, icon: <Icon.Settings/>, component: ClusterSettings, children: null},
];

// dashboard layout routing
export const dashboard = [
    dashboardRoutes,
    ...analysisRoutes,
    ...indicesRoutes,
    ...searchRoutes,
    ...managementRoutes
];

export const intro = [
    clusterRoutes,
    roleRoutes,
    userRoutes,
    editUserRoutes,
];


// auth layout routing
export const auth = [
    homeRoutes,
];
